.Git {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.heading {
    font-size: 45px;
    font-weight: 400;
    text-decoration: underline;
}

.heading2 {
    text-align: center;
    font-size: 25px;
    font-weight: 400;
    margin-bottom: 20px;
    text-decoration: underline;
}



/* Mobile view */
@media screen and (min-width:320px) and (max-width:426.9px) {
    .heading {
        font-size: 35px;
    }

    .heading2 {
        font-size: 15px;
    }
}