#contact {
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid white;
    border-radius: 20px;
    width: 98.5vw;
    margin: auto;
}

#contact>h2 {
    font-size: 45px;
    font-weight: 400;
    text-align: center;
    text-decoration: underline;
    text-decoration-style: solid;
    margin-bottom: 7vh;
}

#contact>div {
    /* border: 1px solid white; */
    display: grid;
    grid-template-columns: repeat(2, auto);
    gap: 20px;
    margin: auto;
    justify-content: space-around;
    width: 100%;
    margin-top: -7px;


}

#contact>div>div {
    margin: 10px;
    display: flex;
    align-items: center;
}

a {
    margin-right: 20px;
    margin-left: 5px;
}

p {
    font-size: 14px;
    text-align: center;
    color: white;
    text-decoration: underline;
}

@media screen and (min-width:427px) and (max-width:768px) {
    #contact {
        display: flex;
        height: auto;
        margin: auto;
    }

    #contact>h2 {
        font-size: 35px;
    }

    #contact>div {
        grid-template-columns: none;
        grid-template-rows: repeat(2, auto);
        row-gap: 5px;
    }

    #contact>div>div {
        flex-direction: column;
        align-items: center;
    }

    a {
        margin-left: none;
        margin-right: none;
        text-align: center;
    }

    p {
        font-size: 12px;
    }
}

@media screen and (min-width:320px) and (max-width:426.9px) {
    #contact {
        height: auto;
        margin: auto;
    }

    #contact>h2 {
        font-size: 35px;
    }

    #contact>div {
        grid-template-columns: none;
        grid-template-rows: repeat(2, auto);
        row-gap: 5px;
    }

    #contact>div>div {
        flex-direction: column;
        align-items: center;
    }

    a {
        margin-left: none;
        margin-right: none;
        text-align: center;
    }

    p {
        font-size: 12px;
    }
}