.Streak {
    display: grid;
    grid-template-columns: repeat(2,30%);
    gap: 30px;
    padding: 30px;
    justify-content: center;
}

/* Tablet view */
@media screen and (min-width:427px) and (max-width:768px) {
    .Streak {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
}

/* Mobile view */
@media screen and (min-width:320px) and (max-width:426.9px) {
    .Streak {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
}