.project {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 50px;
  column-gap: 30px;
  row-gap: 30px;
}

/* Tablet view */
@media screen and (min-width:427px) and (max-width:768px) {
  .project {
    display: flex;
    flex-direction: column;
    row-gap: 30px
  }
}

/* Mobile view */
@media screen and (min-width:320px) and (max-width:426.9px) {
  .project {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
    margin: 20px;
  }
}